import {
  BtnNavContainer,
  ContainerWelcomPage,
  TitleContainer,
  WelcomImg,
  WelcomLogo,
  WelcomTitle,
} from './WelcomePage.styled';
import welcomeImgLarge from 'assets/images/different/children.jpg';
import welcomeImgMedium from 'assets/images/different/children_desctop.jpg';
import welcomeImgSmall from 'assets/images/different/children_mobil.jpg';
import logo from 'assets/images/Header/LogoSunstoriy.jpg';
import { useEffect, useState } from 'react';
import BtnNav from 'ui/BtnNav/BtnNav';

function WelcomePage() {
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => setWindowWidth(window.innerWidth);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  let selectedWelcomeImg;

  if (windowWidth > 768) {
    selectedWelcomeImg = welcomeImgLarge;
  } else if (windowWidth > 375) {
    selectedWelcomeImg = welcomeImgMedium;
  } else {
    selectedWelcomeImg = welcomeImgSmall;
  }

  return (
    <ContainerWelcomPage>
      <div>
        <WelcomImg src={selectedWelcomeImg} alt="children" />
      </div>
      <TitleContainer>
        <WelcomTitle>
                  :
        </WelcomTitle>
        <WelcomLogo src={logo} alt="logo" />
        <WelcomTitle>
              .     
             !
        </WelcomTitle>
      </TitleContainer>
      <BtnNavContainer>
        <BtnNav to="auth/register"></BtnNav>
        <BtnNav to="auth/signin" className="dark">
          
        </BtnNav>
      </BtnNavContainer>
    </ContainerWelcomPage>
  );
}

export default WelcomePage;
