import { useLocation } from 'react-router-dom';
import {
  FooterItem,
  FooterLink,
  FooterNav,
  FooterNavWrapper,
} from './Footer.styled';

const Footer = () => {
  const location = useLocation();
  const currentPath = location.pathname;
  return (
    <>
      <FooterNavWrapper>
        <FooterNav>
          <FooterItem>
            <FooterLink></FooterLink>
          </FooterItem>
          <FooterItem>
            <FooterLink></FooterLink>
          </FooterItem>
          <FooterItem>
            <FooterLink
              active={currentPath === '/financial' ? 'active' : ''}
              to={'/financial'}
            >
              
            </FooterLink>
          </FooterItem>
        </FooterNav>
      </FooterNavWrapper>
    </>
  );
};

export default Footer;
